'use client';

import React from 'react';

interface SmartVideoPlayerProps {
  videoUrl: string;
  posterUrl?: string;
}

export default function SmartVideoPlayer({ videoUrl, posterUrl }: SmartVideoPlayerProps) {
  // 1. ÂÆâÂÖ®Ê£ÄÊü•ÔºöÂ¶ÇÊûú URL ‰∏∫Á©∫ÔºåÁõ¥Êé•ËøîÂõûÁ©∫ÊàñÂç†‰ΩçÁ¨¶
  if (!videoUrl) return <div className="bg-gray-900 aspect-video flex items-center justify-center text-gray-500">Êó†ËßÜÈ¢ëÊ∫ê</div>;

  // 2. Âà§Êñ≠ÊòØÂê¶‰∏∫ Bilibili ÈìæÊé•
  const isBilibili = videoUrl.includes('bilibili.com') || videoUrl.startsWith('BV');

  if (isBilibili) {
    // 3. ÊèêÂèñ BV Âè∑ (ÊîØÊåÅÂÆåÊï¥ÈìæÊé•ÊàñÂçïÁã¨ÁöÑ BV Âè∑)
    const match = videoUrl.match(/(BV\w+)/);
    const bvid = match ? match[1] : null;

    if (!bvid) {
      return <div className="bg-gray-900 text-red-500 p-4">Êó†ÊïàÁöÑ B Á´ôÈìæÊé•</div>;
    }

    // 4. Ê∏≤Êüì B Á´ôÂµåÂÖ•ÂºèÊí≠ÊîæÂô® (Iframe)
    // high_quality=1: ÂºÄÂêØÈ´òÊ∏Ö
    // danmaku=0: ÈªòËÆ§ÂÖ≥Èó≠ÂºπÂπï
    // autoplay=0: ÂÖ≥Èó≠Ëá™Âä®Êí≠Êîæ
    return (
      <div className="relative w-full h-0 pb-[56.25%] overflow-hidden rounded-xl bg-black shadow-2xl border border-white/10">
        <iframe
          src={`//player.bilibili.com/player.html?bvid=${bvid}&page=1&high_quality=1&danmaku=0&autoplay=0`}
          className="absolute top-0 left-0 w-full h-full"
          scrolling="no"
          frameBorder="0"
          allowFullScreen
          // ‚ö†Ô∏è ÂÖ≥ÈîÆÔºöÈò≤Ê≠¢ B Á´ôÂõ†‰∏∫ Referrer Êã¶Êà™ËØ∑Ê±Ç (403 Forbidden)
          referrerPolicy="no-referrer"
        />
      </div>
    );
  }

  // 5. Â¶ÇÊûú‰∏çÊòØ B Á´ôÔºåÈªòËÆ§‰ΩøÁî®ÊôÆÈÄö Video Ê†áÁ≠æ (Êí≠Êîæ MP4)
  return (
    <div className="relative w-full h-auto rounded-xl overflow-hidden bg-black shadow-2xl border border-white/10">
      <video
        src={videoUrl}
        poster={posterUrl}
        controls
        className="w-full h-auto"
        playsInline
      />
    </div>
  );
}